const draw = (e) => {
    // 所有cell都由它来画
    // 逻辑：如果存在dynamic，就画根据dynamic的配置画；如果不存在，就画img
    // log(e)
    const {x, y, alive, staticConf, dynamic} = e
    const {width, height, name} = staticConf
    if (!_.isUndefined(dynamic)) {
        // log(`在(${x}, ${y})处画动图`)
        const {name, width, height, index} = dynamic
        if (alive) {
            const xOffset = (CELL_SIZE - width) / 2
            const yOffset = (CELL_SIZE - height) / 2
            gameCtx.drawImage(images[name], index * width, 0, width, height, x + xOffset, y + yOffset, width, height)
        }
    } else {
        if (alive) {
            const xOffset = (CELL_SIZE - width) / 2
            const yOffset = (CELL_SIZE - height) / 2
            // log(`(${x / 80}, ${y / 80})`, name)
            gameCtx.drawImage(images[name], x + xOffset, y + yOffset)
        }
    }
    // log(mousePosition)
    drawText(gameCtx, arroundArray.length, mousePosition[0], mousePosition[1])
    // drawText(gameCtx, 1, 2, 2)
    // drawText(gameCtx, '4', 1, 2)
    // drawText(gameCtx, '5', 1, 3)
    // drawText(gameCtx, '6', 1, 4)

}


const update = (e) => {
    // 所有的更新都靠update函数
    // 如果在around数组里，则修改状态
    const {dynamic} = e
    if (_.findIndex(arroundArray, (o) => {
        return o.id === e.id && o.x === e.x && o.y === e.y
    }) === -1) {
        if (dynamic) {
            log('不在其中, 且有dynamic')
            delete e.dynamic
        }
    }
    for (let i = 0; i < arroundArray.length; i++) {
        const a = arroundArray[i]
        if (!_.isUndefined(a.dynamic)) {
            // 其中的元素如果没有变亮就让它变亮
        } else {
            getHighlightAnimation(a)
        }
    }

    if (!_.isUndefined(dynamic) && e.alive) {
        // 有dynamic，就更新它！
        // log(`在(${x}, ${y})处画动图`)

        dynamic.index += 1
        if (dynamic.name === 'dynamic_bubble' && dynamic.index >= dynamic.count) {
            // 消除
            e.alive = false
            log('消除:', e)
        }
        dynamic.index %= dynamic.count
    }
}

const getHighlightAnimation = (e) => {
    // 传入一个对象，修改它的配置
    // let e = gameMap[i][j]
    const {x, y, name} = e

    // 获取高亮的动画配置，修改
    const config = highlight_config['highlight_' + name]
    e.dynamic = config
}

const setBubble = (e) => {
    const config = dynamic_config['dynamic_bubble']
    e.dynamic = config
}

const listenDrop = () => {
    // 要不断监测。。是否需要下落
    for (let k = 0; k < 10; k++) {
        // 找到一列中 为空的坐标
        // 一旦遇到了1个空的，那么上面的都要动
        let tag = false
        let step = 0
        for (let l = 9; l >= 0; l--) {
            if (gameMap[l][k].alive === false) {
                step += 1
                tag = true
            }
            if (tag && gameMap[l][k].alive) {
                const start = gameMap[l][k]
                const end = gameMap[l + step][k]

                const startPos = [l, k]
                const endPos = [l + step, k]
                // 需要传入： 到终点之后，把gameMap修改掉
                // 也就是把gameMap的end位置 修改成 start
                drop(k, start, end.y, startPos, endPos)
            }
        }
    }
}

const renderSquare = (ctx) => {
    // drop(2, gameMap[0][1], gameMap[1][1].y, [0, 1], [1, 1])

    // getHighlightAnimation(gameMap[1][2])
    // getHighlightAnimation(2, 2)

    // setBubble(gameMap[1][2])

    setInterval(() => {
        clearSquare(ctx)
        // 也就是update每个cell的配置具体画，还是靠调用draw
        // TODO: 之后也要拆出来单独做
        for (let x = 0; x < height; x += 1) {
            for (let y = 0; y < width; y += 1) {
                const e = gameMap[x][y]
                // log('e:', e)
                update(e)
            }
        }

        // const e = gameMap[0][0]
        // draw(e)

        for (let x = 0; x < height; x += 1) {
            for (let y = 0; y < width; y += 1) {
                const e = gameMap[x][y]
                // 仅仅只做画的操作，给什么画什么
                // 其他更新操作 全部交给updat来做
                log('eee',e)
                draw(e)
            }
        }
        listenDrop()
        // log(gameMap[9])
    }, 200)
}

const drop = (tag, start, endY, startPos, endPos) => {
    // log('下落:', start, endY, endY / CELL_SIZE, start.x / CELL_SIZE, start.y / CELL_SIZE)
    setTimeout(() => {
        if (start.y + 9 > endY) {
            // 修复超出的部分
            start.y = endY
            // log('start:', start, endY)
            // 相当于，杀掉了原来元素。。而不是新的元素
            // 那么在下落的时候，应该是拷贝新的元素，而不是旧的
            // 旧的元素应该杀掉

            // 换位置，那原来位置的值，没有抹除掉。。
            // 相当于，start只有y变化了，其他数据没有变，地图上的值也没变
            const i = endPos[0]
            const j = endPos[1]
            gameMap[i][j] = _.cloneDeep(start)

            // log(startPos, i, j)
            gameMap[startPos[0]][startPos[1]].alive = false
            // log(gameMap[9])
            return;
        }
        start.y += 10
        drop(tag, start, endY, startPos, endPos)

    }, 1000 / 60)
}

const getPos = (x, y) => {
    // 根据坐标 算网格坐标
    return [Math.ceil(x / CELL_SIZE), Math.ceil(y / CELL_SIZE)]
}

const removeAround = (ctx, i, j, type) => {
    if (type === MOUSE_TYPE.LEFT_CLICK && arroundArray.length > 1) {
        // 点击修改状态，爆炸
        // getBubble(gameMap[i][j])
        log('现在点亮的:', arroundArray)
        // 遍历它们，变成bubble
        for (let i = 0; i < arroundArray.length; i++) {
            const a = arroundArray[i]
            setBubble(a)
        }
    }
}

const handleDoubleClick = (event) => {
    const type = event.button
    const [y, x] = getPos(event.offsetX, event.offsetY)
    log('双击', x - 1, y - 1)
    mousePosition = [-1, -1]
    removeAround(gameCtx, x - 1, y - 1, type)
}

const highlightAround = (i, j) => {
    const currentE = gameMap[i][j]
    // log('当前点击:', currentE)
    statusMap[i][j] = 1
    arroundArray.push(currentE)
    log('arroundArray', arroundArray)

    const direction = [[-1, 0], [1, 0], [0, 1], [0, -1]]
    direction.forEach(d => {
        const newX = i + d[0]
        const newY = j + d[1]
        if (newX >= 0 && newY >= 0 && newX < width && newY < height) {
            const newE = gameMap[newX][newY]
            const status = statusMap[newX][newY]
            // log('status:', status)
            // around可以抽出来一个函数来处理，传进去cb
            // 新坐标与当前坐标是同一类型，代表需要点亮.且必须是没有访问过的
            if (newE.name === currentE.name && status === 0) {
                // log('newE/currentE:', newE, currentE, newX, newY)
                statusMap[newX][newY] = 1
                highlightAround(newX, newY)
            }
        }
    })
}

const handleSingleClick = (event) => {
    const [y, x] = getPos(event.offsetX, event.offsetY)
    const i = x - 1
    const j = y - 1
    mousePosition[0] = i
    mousePosition[1] = j
    log('单击:', mousePosition)

    if (arrayEquals(mousePosition, beforeMousePosition)) {
        // 鼠标坐标没变过，则不用更新
        log('鼠标未变')
    } else {
        beforeMousePosition[0] = i
        beforeMousePosition[1] = j
        // 清空一次数组
        arroundArray = []
        highlightAround(i, j)
        statusMap = getEmptySquare(width, height)
    }
}

const bindEventDelegate = function (canvas) {
    // canvas.addEventListener('mousedown', (event) => {
    //     startX = event.offsetX
    //     startY = event.offsetY
    //     log(startX, startY)
    // })

    // canvas.addEventListener('mouseup', (event) => {
    //     endX = event.offsetX
    //     endY = event.offsetY
    //     log(endX, endY)
    // })
    canvas.addEventListener('mousedown', handleSingleClick)
    canvas.addEventListener('dblclick', handleDoubleClick)
}

