// 获取静态图片
const getStaticElement = (square, i, j) => {
    // 画出来的坐标 要和原数组一致。。。
    const name = IMG_MAPPER[square[i][j]]
    const e = {
        id: square[i][j],
        name: name,
        x: j * CELL_SIZE,
        y: i * CELL_SIZE,
        alive: true,
        staticConf: _.cloneDeep(static_config[name]),
    }
    return e
}

const genMap = (square) => {
    let output = []
    for (let x = 0; x < height; x++) {
        let t = []
        for (let y = 0; y < width; y++) {
            const e = getStaticElement(square, x, y)
            t.push(e)
        }
        output.push(t)
    }
    log('map', output)
    return output
}

const _init = () => {
    const square = getSquare()
    gameMap = genMap(square)
    statusMap = getEmptySquare(width, height)

    _start()
}

const loadAssets = (config, container) => {
    // load resume files
    let names = Object.keys(config)
    let count = 0
    for (let i = 0; i < names.length; i++) {
        let name = names[i]
        let path = config[name].path
        let img = new Image()
        img.src = path
        // img加载完成后执行
        img.onload = function () {
            container[name] = img
            count += 1
            if (count == names.length) {
                // 开始运行逻辑代码
                // _start()
                _init()
            }
        }
    }
}

const run = () => {
    // 加载资源
    const assets = {
        ...static_config,
        ...highlight_config,
        ...dynamic_config,
    }
    loadAssets(assets, images)
}

const _main = () => {
    run()
}

_main()
